$white: #fff;
$gray: #ccc;
$darkgray: #8e8e8e;
$titlegray: #797874;
$shadow: #656565;
$textgray: #555;
$darkergray: #2c2c2c;
$black: #000;
$blue: #005293;
$red: #ae2118;
$yellow: #ffff00;
$arialFont: Helvetica, Arial;
$tablet: '(min-width: 941px)';  /* NOTE: it was 768px originally, but overridden for Zampotta weird theme*/
$tabletLarge: '(min-width: 1024px)';

.nx-storelocator {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: $textgray;
    float: left;
    /*font: normal 14px/1.4 $baseFont;*/
    width: 100%;

    /* Avoid issues with Google Maps and CSS frameworks */
    > * {
        -webkit-box-sizing: content-box !important;
                box-sizing: content-box !important;
    }
}

/* InfoBubble font size */
.nx-storelocator__window {
	font-size: 13px;
}

.nx-storelocator__error {
	clear: both;
	color: $red;
	float: left;
	font-weight: bold;
	padding: 10px 0;
	width: 100%;
}

.nx-storelocator__loading {
    background: url(../img/ajax-loader.gif) no-repeat;
    float: left;
    margin: 4px 0 0 10px;
    height: 16px;
    width: 16px;
}

/* Avoid image issues with Google Maps and CSS resets */
.nx-storelocator__map-container img {
    border-radius: 0 !important;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
    max-height: none !important;
    max-width: none !important;
}

/*
 * Form and controls
 */
.nx-storelocator__form {
    clear: left;
    float: left;
    margin-top: 15px;
    width: 100%;
}

.nx-storelocator__search-box {
    float: left;
    margin-top: 3px;
    margin-bottom: 7px;
    width: 100%;

    /* Tablet up*/
    @media #{$tablet} {
        width: auto;
    }
}

.nx-storelocator__form-label {
    display: block;
    font-weight: bold;
    width: 100%;

    /* Tablet up*/
    @media #{$tablet} {
        display: inline-block;
        width: auto;
    }
}

.nx-storelocator__input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /*font: normal 14px/1.4 $baseFont;*/
    margin: 15px 0;
    padding: 6px 12px;
    width: 100%;

    /* Tablet up*/
    @media #{$tablet} {
        width: auto;
        margin: 0 15px 0 10px;
    }
}

    .nx-storelocator__loc-detail--dist.nx-storelocator__loc-detail--alt {
		display: none;
	}

.nx-storelocator__button {
    margin-top: 3px;
    white-space: nowrap;
}
.nx-storelocator__button--search {
    clear: both;
    margin-top: 6px;
}
