/** Filters */
.nx-storelocator__filters-container {
    clear: both;
    float: left;
    margin: 15px 0;
    width: 100%;
}
.nx-storelocator__filters {
    float: left;
    list-style: none;
    margin: 0 100px 0 0;
    padding: 0;

    li {
        clear: left;
        display: block;
        float: left;
        margin: 5px 0;
        width: 100%;

        label {
            display: inline;
            vertical-align: text-bottom;
        }

        input {
            display: block;
            float: left;
            margin-right: 8px;
        }

        select {
            -webkit-box-sizing: border-box;
                    box-sizing: border-box;
            /*font: normal 14px/1.4 $baseFont;*/
            padding: 6px 12px;
        }
    }
}

.nx-storelocator__map-container {
    clear: left;
    float: left;
    margin-top: 15px;
    width: 100%;

    /* Tablet large up*/
    @media #{$tabletLarge} {
        margin-bottom: 30px;
    }

    a {
        /*color: $blue;*/
        text-decoration: none;

        &:active,
        &:focus,
        &:hover {
            text-decoration: underline;
        }
    }
}

/*
 * Locations list
 */
.nx-storelocator__loc-list-container {
    font-size: 13px;
    height: 530px;
    overflow-x: auto;
    width: 100%;

    /* Tablet large up*/
    @media #{$tabletLarge} {
        &:not(.nx-storelocator__loc-list-container--map-hidden) {
            width: 30%;
        }
    }
}

.nx-storelocator__loc-list {
    display: block;
    clear: left;
    float: left;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nx-storelocator__loc-item {
    border: 1px solid transparent; /* Adding this to prevent moving li elements when adding the list-focus class*/
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    clear: left;
    cursor: pointer;
    display: inline-block;
    float: left;
    width: 100%;
}
.nx-storelocator__loc-item--selected {
    border: 1px solid rgba(0, 82, 147, 0.4);
    -webkit-transition: border 0.2s linear 0s, -webkit-box-shadow 0.2s linear 0s;
    transition: border 0.2s linear 0s, -webkit-box-shadow 0.2s linear 0s;
    transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s;
    transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s, -webkit-box-shadow 0.2s linear 0s;
}

.nx-storelocator__loc-label {
    display: block;
    float: left;
    width: 30px;
    margin: 10px 0 0 10px;
    padding: 3px 0 2px 1px;
    background: darken( $blue, 20% );
    border-radius: 99px;
    color: $white;
    font-family: $arialFont;
    font-weight: bold;
    text-align: center;
}

.nx-storelocator__loc-details {
    height: inherit;
    overflow: hidden;
}

.nx-storelocator__loc-content {
    padding: 10px;
    line-height: 18px;
}

.nx-storelocator__loc-detail--dist {
    color: $darkgray;
    font-style: italic;
}
.nx-storelocator__loc-detail--name {
    font-size: 15px;
    font-weight: bold;
}
.nx-storelocator__loc-detail--features {
    font-style: italic;
}
.nx-storelocator__loc-detail--addr {
    margin-top: 5px;
}

/*
 * Directions
 */
.nx-storelocator__close-directions-container {
    height: 20px;
    position: relative;
    width: 100%;
}

.nx-storelocator__close-icon {
    right: 6px;
    top: 0;
}

.nx-storelocator__directions-panel {
    margin: 0 2%;

    /* Avoid issues with table-layout */
    table {
        table-layout: auto;
        width: 100%;
    }

    table, td {
        vertical-align: middle;
        border-collapse: separate;
    }

    td {
        padding: 1px;
    }

    .adp-placemark {
        margin: 10px 0;
        border: 1px solid #c0c0c0;
    }

    .adp-marker {
        padding: 3px;
    }
}

.nx-storelocator__noresults-title {
    font-weight: bold;
    font-size: large;
    margin: 15px;
}

.nx-storelocator__noresults-desc {
    margin: 0 15px;
}

.nx-storelocator__map {
    float: left;
    height: 530px;
    width: 100%;

    /* Tablet large up*/
    @media #{$tabletLarge} {
        width: 70%;
    }
}

.nx-storelocator__pagination-container {
    clear: both;

    ol {
        list-style-type: none;
        margin: 0;
        padding: 10px 0;
        text-align: center;

        li {
            color: $blue;
            cursor: pointer;
            display: inline-block;
            font-weight: bold;
            /*font: bold 14px $baseFont;*/
            padding: 10px;
        }

        .nx-storelocator__current {
            color: $textgray;
            cursor: auto;
            text-decoration: none;
        }
    }
}


/*
 * Modal window
 */
.nx-storelocator__overlay {
	background: url(../img/overlay-bg.png) repeat;
	height: 100%;
	left: 0;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 10000;
}

.nx-storelocator__modal-window {
    background: $white;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 10px #656565;
            box-shadow: 0 0 10px #656565;
    position: absolute;
    left: 50%;
    margin-left: -460px; /* width divided by 2 */
    margin-top: 60px;
    height: 620px;
    width: 920px;
    z-index: 10010;
}

.nx-storelocator__map-container {
    margin-top: 50px; /* increase map container margin */
}

.nx-storelocator__modal-content {
    float: left;
    padding: 0 1%; /* there's already a margin on the top of the map-container div */
    width: 98%;
}

.nx-storelocator__close-icon {
    right: 22px;
    top: 13px;
}

.nx-storelocator__close-icon {
	cursor: pointer;
	height: 24px;
	position: absolute;
	width: 24px;

	&:after,
	&:before {
		background: $gray;
		content: '';
		display: block;
		height: 24px;
		margin: -3px 0 0 -1px;
		position: absolute;
		bottom: 0;
		left: 50%;
		right: 3px;
		top: 3px;
		width: 3px;
		-webkit-transform: rotate(45deg);
		    -ms-transform: rotate(45deg);
		        transform: rotate(45deg);
	}

	&:hover:after,
	&:hover:before {
		background: darken($gray, 10%);
	}

	&:before {
		-webkit-transform: rotate(-45deg);
		    -ms-transform: rotate(-45deg);
		        transform: rotate(-45deg);
	}
}
